.header{
    z-index: 100;
    width: 96%;
    height: 70px;
    color: #000;
    padding: 8px 2%;
    display: flex;
    flex-direction: row ;
    justify-content: space-between;
    .header-logo{
        position: relative;
        display: inline-block;
        align-items: center;
        align-content: center;
        cursor: pointer;
        img{
            border-radius: 10px;
            height: 30px;
            user-select: none; /* Standard syntax */
            -moz-user-select: none; /* Firefox */
            -webkit-user-select: none; /* Chrome, Safari */
            -ms-user-select: none; /* Internet Explorer/Edge */
            pointer-events: none; /* Prevent pointer events */
        }
        .overlay {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: all; /* Enable pointer events */
            background: rgba(255, 255, 255, 0); /* Transparent overlay */
        }
        @media (max-width: 600px) {
            img{
                height: 24px;
            }
        }


        transform: scale(1);
        transition: transform 0.5s ease;
    }
    .header-logo:hover {
        transform: scale(1.1);
    }
    .header-nav{
        display: flex;
        justify-content: end;
        width: 120px;
        .upload-div{
            height: 50%;
            width: 40%;
            margin: auto;
            transition: width 0.5s ease;
            color: #565656;
            background-color: #f5f5f5;
            border-radius: 30px;
            align-content: center;
            display: flex;
            flex-direction: row;
            justify-content: center;
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            font-weight: 600;
            box-shadow: 0 0px 13px rgba(0, 0, 0, 0.180);
            cursor: pointer;
            .text{
                margin: auto;
                display: none;
                opacity: 0;
                transition: opacity 1s ease; 
            }
            .icon{
                opacity: 1;
                height: 40%;
                margin: auto 0px;
                
            }
            .icon-img{
                opacity: 1;
                height: 80%;
                margin: auto 0px;
            }
        }
        .upload-div:hover {
            width: 100%;
            .text {
                opacity: 1;
                display: block;
            }
            .icon{
                display: none;
                opacity: 0;
            }
            .icon-img{
                display: none;
                opacity: 0;
            }
        }
        img{
            border-radius: 50px;
            cursor: pointer;
        }
        @media (max-width: 600px) {
            .upload-div{
                width: 50%;
            }
        }
    }
    @media (max-width: 600px) {
        .header-nav{
            width: 80px;
            .upload-div{
                height: 60%;
                width: 50%;
            }
        }
    }
}